
import { Options, Vue } from 'vue-class-component';
import {NumberControl} from "@/logic/Filter";
import {PropType} from "vue";
import {formatDecimal, roundDecimals} from "@/utils";

@Options({
  components: {},
  props: {
    control: Object as PropType<NumberControl>,
    value: Number
  },
  emits: [
    'input'
  ],
  watch: {
    'value': 'valueChanged',
    'state': 'stateChanged'
  }
})
export default class NumberInput extends Vue {
  control!: NumberControl
  value!: number

  valid: boolean = true
  focused: boolean = false
  state: string = ''

  get useSteppers() {
    return this.control.step !== undefined
  }

  get inputMode() {
    return this.control.precision == 0 ? 'numeric' : 'decimal';
  }

  valueChanged() {
    if(!this.focused)
      this.updateState()
  }

  stateChanged() {
    let pattern = this.control.precision == 0 ? NumberInput.int_regex : NumberInput.float_regex
    this.valid = pattern.test(this.state)
    let value = roundDecimals(parseFloat(this.state), this.control.precision)
    this.valid &&= this.boundsCheck(value)
    if(!this.valid)
      return
    this.$emit('input', value)
  }

  stepDown() {
    let step = this.control.step
    if(step === undefined) return
    let value = roundDecimals(this.value - step, this.control.precision)
    if(this.boundsCheck(value))
      this.$emit('input', value)
  }

  stepUp() {
    let step = this.control.step
    if(step === undefined) return
    let value = roundDecimals(this.value + step, this.control.precision)
    if(this.boundsCheck(value))
      this.$emit('input', value)
  }

  boundsCheck(value: number): boolean {
    if(this.control.min !== undefined && value < this.control.min)
      return false
    if(this.control.max !== undefined && value > this.control.max)
      return false
    return true
  }

  focus() {
    this.focused = true
  }

  blur() {
    this.focused = false
    this.valid = true
    this.updateState()
  }

  mounted() {
    this.updateState()
  }

  private updateState() {
    this.state = formatDecimal(this.value, this.control.precision)
  }

  static float_regex = /^\s*-?(?:\d*\.\d+|\d+\.\d*|\d+)\s*$/
  static int_regex = /^\s*-?\d+\s*$/
}
